.trend-contianer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    width: 25em;
    box-shadow: 0px 2px 37px 0px rgba(0, 0, 0, 0.75);
    border-radius: 1rem;
    background-color: white;
}


.trend-contianer h2 {
    display: flex;
    padding-top: 1em;
    justify-content: center;
    font-family: 'Comic Neue', cursive;
}

.trend-contianer img {
    width: 35px;
    justify-content: center;
    align-items: center;
}

.trend-cards {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 1em;
    border-bottom: solid rgba(0, 0, 0, 0.233) 1px;
}

.trend-titles {
    display: flex;
    justify-content: space-between;
    padding: 1.5em;
}

@media (max-width: 820px) {

    .trend-contianer {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 90%;
    }
}