@import url('https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap');


.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 5em;
    color: white;
    background-color: #222831;
}

.navbar h1:hover {
    color: #F2DF3A;
    cursor: pointer;
}

.nav-links {
    display: flex;
    align-items: center;
    padding-right: 6em;
    gap: 1em;
}

.main-title {
    font-family: 'Comic Neue', cursive;
    padding-left: 2em;
    font-size: 2rem;
}

a {
    font-family: 'Comic Neue', cursive;
    color: white;
    list-style: none;
    text-decoration: none;
    font-size: 1.25rem;
}


a:hover {
    color: #F2DF3A;
}


@media (max-width: 820px) {

    .navbar {
        width: 100%;
    }

    .nav-links {
        padding: 1em;
        margin: auto;
    }

    .main-title {
        display: none;
    }
}