.trending-articles {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.home-container {
    display: flex;
    justify-content: center;
    gap: 1em;
    width: 100%;
}

.sidebar {
    display: flex;
    flex-direction: column;
    width: 20em;
    min-width: 350px;
    margin-top: 2em;
}



@media (max-width: 820px) {

    .trending-articles {
        display: flex;
        flex-direction: column-reverse;
        width: 100%;
        padding: 0;
    }


    .home-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        gap: 0;
    }

    .sidebar {
        display: flex;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

}