@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');


.topnews-container {
    margin-top: 2em;
    max-width: 50em;
    border-radius: 1rem;
    padding: 0 2em;
    background-color: rgb(255, 255, 255);
}


.topnews-container img {
    margin: auto;
    width: 15em;
    margin: 1em 0;
}

.topnews-card {
    display: flex;
    align-items: center;
    border-bottom: solid 1px rgba(0, 0, 0, 0.281);
    gap: 1em;
}



.topnews-card a {
    color: black;
    font-weight: bold;
    cursor: pointer;
}

.topnews-card a:hover {
    color: #222831dc;
    text-decoration: underline;
}


.topnews-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
}

.card-info {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    line-height: 1.3rem;
    font-size: .8rem;
}

.card-info p {
    padding-top: 1em;
}


@media (max-width: 820px) {

    .topnews-container {
        display: flex;
        flex-direction: column;
        width: 90%;
        min-width: 350px;
        padding: 0 1em;
        margin: 2em 0;
    }

    .topnews-card {
        display: flex;
        flex-direction: column;
    }

    .topnews-container img {
        min-width: 300px;
    }

    .card-info {
        padding: 0 1.25em;
        font-size: .8rem;
    }




}