.coin-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: auto;
    gap: 1em;
    width: 85%;
    margin-top: 3em;
}


.coin-cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 1rem;
    background-color: white;
    cursor: pointer;
    height: 12em;
    animation: myAnim 1s ease 0s 1 normal forwards;
}

span.market_rank {
    display: flex;
    flex-direction: row;
}

.coin-cards h2 {
    padding: 0;
    margin: 0;
}

.coin-cards p {
    padding: 0;
    margin: 0;
}



.coin-cards:hover {
    border-radius: 1em;
}

.coin-percent {
    font-size: 1.25rem;
}



.coin-percent.red {
    color: red;
}

.coin-percent.green {
    color: green;
}



.modalCard {
    display: block;
    background-color: aqua;
    width: 50em;
}


/*  Popup card styling   */

.popup-card {
    width: 100%;
    height: 20em;
    background-color: white;
    border-radius: 1rem;
    margin-top: -25px;
}

.popup-card h3 {
    display: flex;
    flex-direction: row;
    float: right;
    padding: 1em;
}

.popup-card img {
    width: 15px;
    float: right;
    cursor: pointer;
    margin-right: 0.5em;
    margin-top: -25px;
}

.popup-info {
    padding: 3em 2em;
}

li {
    list-style: none;
    margin: 1em;
}


@keyframes myAnim {
    0% {
        opacity: 0;
        transform: rotateX(-100deg);
        transform-origin: top;
    }

    100% {
        opacity: 1;
        transform: rotateX(0deg);
        transform-origin: top;
    }
}


img {
    width: 60px;
    padding-top: 1em;
}







/* Small screen sizes */
@media (max-width: 1200px) {

    .coin-grid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        justify-content: center;
        padding: 0;
        margin: 2em auto;
        gap: 1em;
        width: 90%;

    }

}




/*  Mobile  */
@media (max-width: 820px) {

    .coin-grid {
        display: grid;
        grid-template-columns: 1fr;
        justify-content: center;
        padding: 0;
        margin: 2em auto;
        gap: 1em;
        width: 90%;

    }

    .coin-cards {
        width: 100%;

    }

    img {
        width: 40px;
    }

    main {
        width: 100%;
        padding: 0;
        margin: 0;
    }


    .popup-card {
        width: 100%;
    }

}